import React,{useState} from 'react'
import './Navbar.css';

export default function TextForm(prop) {
    
    const[text,setText] = useState('');

    const handleUpClick=()=>{
        // console.log("Uppercase was clicked"+text);

        
        let newText = text.toUpperCase();
        setText(newText);
    }
    const handleLoClick=()=>{
        let newText=text.toLowerCase();
        setText(newText);
    }
    
    const handleClearTextClick=()=>{
        setText('');
    }


    //     COPY TEXT 
    const handleCopy=()=>{
        navigator.clipboard.writeText(text);
        prop.showAlert('copied to clipboard','success');
    }

    //Remove Extra Spaces
    const handleExtraSpaces=()=>{
        let newText=text.split(/[ ]+/);
        setText(newText.join(" "));
    }


    
    const handleOnChange=(event)=>{
        console.log("on Change");
        setText(event.target.value);
    }
    // setText('JAISON')

    return (
        <>
            <div className="container">
            <h1>{prop.heading}</h1>
            <div className="mb-3">
            <textarea className="form-control" id="MyBox" value={text} onChange={handleOnChange} rows="10" style={{backgroundColor: prop.Mode==='dark'?'#262626':'white',color: prop.Mode==='dark'?'white':'black'}}></textarea>
                <div className="My__box">
                        <button disabled={text.length===0} className="btn btn-primary mt-2" onClick={handleUpClick}>Convert to Uppercase</button> 
                        <button disabled={text.length===0} className="btn btn-primary mt-2" onClick={handleLoClick}>Convert to lowerCase</button>
                        <button disabled={text.length===0} className="btn btn-primary mt-2" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
                        <button disabled={text.length===0} className="btn btn-primary mt-2" onClick={handleCopy}>CopyText</button>
                        <button disabled={text.length===0} className="btn btn-primary mt-2" onClick={handleClearTextClick}>Clear Text</button>
                </div>
            </div>
            </div>

            <div className="container my-3">
                <h1>Your text summary</h1>
                <p>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words {text.length} characters</p>
                <p>{0.008*text.split('').length} Minutes Read</p>
                <h2>Preview</h2>
                <p>{text.length>0?text:"Nothing to preview !"}</p>
            </div>
        </>
    )
}
