import React from "react";
import propTypes from "prop-types";
import { Link } from "react-router-dom";
import './Navbar.css';

export default function Navbar(props) {
  return (
    <>
      <nav className={`navbar navbar-expand-lg navbar-${props.Mode} bg-${props.Mode}`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            {props.title}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNavDropdown"
            aria-controls="navbarNavDropdown"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse custom" id="navbarNavDropdown">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link " aria-current="page" to="/textutils">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about">
                  {props.aboutText}
                </Link>
              </li>
            </ul>
              <div className={`form-check form-switch text-${props.Mode === 'light'?'dark':'light'}`}>
                <div></div>
                <input className="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault"/>
                <label className="form-check-label" htmlFor="flexSwitchCheckDefault" >Enable {props.navmode} Mode</label>
              </div>
          </div>
        </div>
      </nav>
    </>
  );
}

Navbar.propType = {
  title: propTypes.string.isRequired,
  aboutText: propTypes.string
};

Navbar.defaultProps={
  title: 'Nithiindcbfd',
  aboutText:'Lol'
};